.top {
	position: relative;
	background-color: #ccc;
	background-image: url(../img/top-bg-fon.jpg);
	@include cover;
	color: #ffffff;
	padding-top: 79px;
	overflow: hidden;
	@include r(805) {
		padding-top: 30px; }
	@include r(420) {
		padding-top: 79px;
		// background: #000 url(../img/top-bg-fon.jpg)
		// background-size: cover
		// background-position: 50%
 }		// background-repeat: no-repeat
	.title-h1 {
		animation-delay: 0.3s; }
	&__photo {
		max-width: 75%;
		max-height: 95%;
		position: absolute;
		bottom: -2%;
		left: 15%;
		z-index: 0;
		filter: drop-shadow( 0 0 10px rgba(255, 255, 255, 0.51));
		@include r(1300) {
			left: 0; }
		@include r(1100) {
			max-width: 70%;
			left: -35px;
			bottom: -1.75%; }
		@include r(500) {
			max-width: 90%;
			left: -15%;
			height: 90%; } }
	&__text {
		opacity: 0.9;
		@include H;
		@include css-lock(14, 22, 480, 960);
		line-height: 1.3em;
		// font-size: em(22)
		margin: 1.6em 1em 1.7em 50%;
		animation-delay: 0.6s;
		@include r(420) {
			margin: 1em 0 1em 50%; } }
	&__button {
		margin-bottom: 4.45em;
		@include css-lock(13, 23, 480, 960);
		// font-size: 23px
		margin-left: 50%;
		animation-delay: 0.9s;
		@include r(420) {
			margin-bottom: 1em; } }
	&__line {
		position: relative;
		z-index: 2;
		text-align: center;
		@include PTs-bi;
		@include css-lock(14, 24, 480, 960);
		// font-size: em(24)
		line-height: 2.3em;
		background-image: linear-gradient(to right, transparent 3%, rgba(203, 52, 97, 0.93) 15%, rgba(203, 52, 97, 0.93) 85%,  transparent 97%);
		@include r(580) {
			height: auto;
			line-height: 1.4em; } }
	.title-h1 {
		margin-left: 50%;
		margin-top: 2.1em;
		@include r(420) {
			margin-left: 50%;
			margin-top: 0;
			font-size: 18px; } } }
