  font-weight: normal;
  line-height: 1em;
}

@media (max-width: 480px) {
  .address__info {
    font-size: 14px;
  }
}

@media (min-width: 960px) {
  .address__info {
    font-size: 18px;
  }
}

@media only screen and (max-width: 830px) {
  .address__info {
    width: 95%;
  }
}

.google-map-box {
  position: relative;
  font-size: calc(30px + (62.5 - 30) * ((100vw - 480px) / (960 - 480)));
  height: 625px;
  height: 10em;
}

@media (max-width: 480px) {
  .google-map-box {
    font-size: 30px;
  }
}

@media (min-width: 960px) {
  .google-map-box {
    font-size: 62.5px;
  }
}

.google-map {
  height: 100%;
  width: 100%;
}
