.opinions {
	text-align: center;
	margin-bottom: 3.3em;
	.opinion {
		display: inline-block;
		max-width: 33%;
		vertical-align: top;
		@include r(850) {
			max-width: 49%; }
		@include r(420) {
			max-width: 90%; } } }
.opinion {
	text-align: center;
	&:after {
		content: "";
		display: inline-block;
		width: 60px;
		height: 4px;
		@include bcpin;
		margin: 0.8em 0; }
	&__img {
		position: relative;
		text-align: center;
		width: 192px;
		height: 192px;
		margin: 0 auto;
		border-radius: 50%;
		overflow: hidden;
		img {
			max-width: 100%;
			max-height: 100%;
			@include vcenter; } }
	&__title {
		color: #4f4b4b;
		@include H;
		@include css-lock(12, 16, 480, 960);
		// font-size: 16px
		line-height: 2.625em; }
	&__text {
		color: #7d7d7d;
		@include PTs;
		@include css-lock(16, 20, 480, 960);
		// font-size: 20px
		line-height: 1.05em; } }
.opinions-box {
	@include clr;
	font-size: 0;
	margin-top: 30px;
	padding-bottom: 60px;
	@include r(800) {
		padding-bottom: 10px; } }
.opinion.is-horizontal {
	@include css-lock(16, 20, 480, 960);
	position: relative;
	text-align: left;
	float: left;
	padding-left: 140px;
	padding-right: 20px;
	margin-top: 30px;
	@include clr;
	width: 50%;
	@include r(650) {
		float: none;
		width: 90%;
		margin: 30px auto;
		padding-left: 100px;
		padding-right: 0; }
	&:after {
		content: "";
		display: inline-block;
		margin: 0; }
	.opinion__img {
		position: absolute;
		left: 0;
		display: block;
		max-width: 109px;
		max-height: 109px;
		@include r(650) {
			left: -2%;
			max-width: 90px;
			max-height: 90px; } }
	.opinion__title {
			line-height: 2em; } }


.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.5); }
