.problems {
	padding-bottom: 4.4em;
	@include r(860) {
		padding-bottom: 1em; }
	.title-h2 {
		margin-bottom: 1.26em;
		@include r(860) {
			margin-bottom: 0.5em; } } }

.problem_gray {
	background-color: #f8f8f8; }
.problem {
	position: relative;
	color: #4f4b4b;
	padding: 1.9em;
	@include clr;
	@include r(860) {
		padding: 0.5em; }
	&__title {
		float: left;
		@include PTs-bold;
		@include css-lock(15, 20, 480, 960);
		// font-size: 20px
		line-height: 1.05em;
		max-width: 23%;
		@include r(860) {
			max-width: 30%; }
		@include r(420) {
			max-width: 37%; } }
	&__arrow {
		display: inline-block;
		position: relative;
		top: 1em;
		left: -0.51em;
		// +s(problem-arrow)
		@include r(860) {
			@include vcenter;
			transform: scale(0.75);
			left: 20%; }
		@include r(650) {
			left: 15%;
			transform: scale(0.45); }
		@include r(575) {
			left: 9%; }
		@include r(490) {
			left: 5%;
			transform: scale(0.3); }
		@include r(420) {
			display: none; } }
	&__about {
		float: right;
		max-width: 50%;
		@include css-lock(13, 16, 480, 960);
		// font-size: 16px
		line-height: 1.3125em;
		@include r(575) {
			max-width: 60%; } } }
